import React from 'react';
import PropTypes from 'prop-types';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import TableSortLabel from '@material-ui/core/TableSortLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Tooltip from '@material-ui/core/Tooltip';
import EditOriginal from './EditOriginal';
import ViewOriginal from './ViewOriginal';
import Button from '@material-ui/core/Button';

//
{/*
numeric:span
disablePaddingpadding
*/}

const rows = [
    { id: 'companyName', numeric: false, disablePadding: true, label: '' },
    { id: 'originalListString', numeric: true, disablePadding: false, label: '' },
    { id: 'tEMPLATE_SELECT', numeric: true, disablePadding: false, label: '' },
    { id: 'remark', numeric: true, disablePadding: false, label: '' },
    {
        id: 'updatePagebutton',
        numeric: false,
        disablePadding: false,
        label: '',
        Cell: ({ row }) =>
            (<ViewOriginal fetchTemplate={this.fetchTemplate} templeteId={row.tEMPLATE_ID} ></ViewOriginal>)
    },
    {
        id: 'updatePagebutton',
        numeric: false,
        disablePadding: false,
        label: '',
        Cell: ({ row }) =>
            (<ViewOriginal fetchTemplate={this.fetchTemplate} templeteId={row.tEMPLATE_ID} ></ViewOriginal>)
    },
    {
        id: 'updatePagebutton',
        numeric: false,
        disablePadding: false,
        label: '',
        Cell: ({ row }) =>
            (<ViewOriginal fetchTemplate={this.fetchTemplate} templeteId={row.tEMPLATE_ID} ></ViewOriginal>)
    },
    {
        id: 'updatePagebutton',
        numeric: false,
        disablePadding: false,
        label: '',
        Cell: ({ row }) =>
            (<ViewOriginal fetchTemplate={this.fetchTemplate} templeteId={row.tEMPLATE_ID} ></ViewOriginal>)
    },
    {
        id: 'updatePagebutton',
        numeric: false,
        disablePadding: false,
        label: '',

        Cell: ({ row }) =>
            (<EditOriginal editTemplate={this.editTemplate} fetchTemplate={this.fetchTemplate} templeteId={row.tEMPLATE_ID} />)
    },
    {
        id: 'delbutton',
        numeric: false,
        disablePadding: false,
        label: '',
        Cell: ({ row }) => (<Button size="small" variant="text" color="primary" onClick={() => { this.confirmDelete(row.tEMPLATE_ID) }}></Button>)
    }
];

// 
class EnhancedTableHead extends React.Component {
    render() {
        const { onSelectAllClick, order, orderBy, numSelected, rowCount } = this.props;
        return (
            <TableHead>
                <TableRow  >
                  
                    {rows.map(
                        row => (
                            <TableCell
                                className="TableCell"
                                key={row.id}
                                align="center"
                                padding="none"
                            >
                                {/*hover*/}
                                <Tooltip
                                    title={row.label}
                                    placement={row.numeric ? 'bottom-end' : 'bottom-start'}
                                    enterDelay={300}
                                >
                                    {/*hover*/}
                                    <TableSortLabel
                                        className="TableSortLabel"
                                        hideSortIcon={true}
                                    >
                                        {row.label}
                                    </TableSortLabel>
                                </Tooltip>
                            </TableCell>
                        ),
                        this,
                    )}
                </TableRow>
            </TableHead>
        );
    }
}
// 
EnhancedTableHead.propTypes = {
    numSelected: PropTypes.number.isRequired,
    // onRequestSort: PropTypes.func.isRequired,
    onSelectAllClick: PropTypes.func.isRequired,
    rowCount: PropTypes.number.isRequired,
};
export default EnhancedTableHead