import React from 'react';
import PropTypes from 'prop-types';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import TableSortLabel from '@material-ui/core/TableSortLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Tooltip from '@material-ui/core/Tooltip';
import EditgeneralContract from './EditGeneralContract.js';
import ViewgeneralContract from './ViewGeneralContract.js';
import Button from '@material-ui/core/Button';
require('./styles/GeneralContract.css')
require('./styles/GeneralContractEnhancedTableHead.css')
//
{/*
numeric:span
disablePaddingpadding
*/}

const rows = [
    { id: 'companyName', numeric: false, disablePadding: true, label: '' },    
    { id: 'contractType', numeric: true, disablePadding: false, label: '' },
    { id: 'contractCreateDate', numeric: true, disablePadding: false, label: '' },
    { id: 'registerArea', numeric: true, disablePadding: false, label: '' },
    { id: 'contractDate', numeric: true, disablePadding: false, label: '' },    
    { id: 'contractPrice', numeric: true, disablePadding: false, label: '' },  
    { id: 'advancesReceived', numeric: true, disablePadding: false, label: '' },  
    { id: 'finalPayment', numeric: true, disablePadding: false, label: '' },      
    {
        id: 'updatePagebutton',
        numeric: false,
        disablePadding: false,
        label: '',
        Cell: ({ row }) =>
            (<ViewgeneralContract fetchTemplate={this.fetchTemplate} generalContractId={row.generalContractId} ></ViewgeneralContract>)
    },
    {
        id: 'updatePagebutton',
        numeric: false,
        disablePadding: false,
        label: '',

        Cell: ({ row }) =>
            (<EditgeneralContract editTemplate={this.editTemplate} fetchTemplate={this.fetchTemplate} templeteId={row.generalContractId} />)
    },
    {
        id: 'delbutton',
        numeric: false,
        disablePadding: false,
        label: '',
        width: 70,
        Cell: ({ row }) => (<Button size="small" variant="text" color="primary" onClick={() => { this.confirmDelete(row.generalContractId) }}></Button>)
    }
];

// 
class GeneralContractEnhancedTableHead extends React.Component {
    render() {
        const { onSelectAllClick, order, orderBy, numSelected, rowCount } = this.props;
        return (
            <TableHead>
                <TableRow  >
                   
                    {rows.map(
                        row => (
                            <TableCell
                                className="GeneralLitsTitle"
                                key={row.id}
                                align="center"
                                padding="none"
                            >
                                {/*hover*/}
                                <Tooltip
                                    title={row.label}
                                    placement={row.numeric ? 'bottom-end' : 'bottom-start'}
                                    enterDelay={300}
                                >
                                    {/*hover*/}
                                    <TableSortLabel
                                        className="TableSortLabel"
                                        hideSortIcon={true}
                                    >
                                        {row.label}
                                    </TableSortLabel>
                                </Tooltip>
                            </TableCell>
                        ),
                        this,
                    )}
                </TableRow>
            </TableHead>
        );
    }
}
// 
GeneralContractEnhancedTableHead.propTypes = {
    //numSelected: PropTypes.number.isRequired,
    // onRequestSort: PropTypes.func.isRequired,
   // onSelectAllClick: PropTypes.func.isRequired,
    //rowCount: PropTypes.number.isRequired,
};
export default GeneralContractEnhancedTableHead